.Home {
    height: 100vh;
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.2)), url('https://media.istockphoto.com/photos/office-leather-desk-table-with-coffee-and-supplies-picture-id514411672?k=6&m=514411672&s=612x612&w=0&h=UMF2nBVbGPXsQyPRX21to22tufvXEwudpd3w6hCXkyw=');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    color: white;
    display: flex;
    align-items: center;
}

.header {
    width: 100%;
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.avatar {
    height: 15rem;
    width: 15rem;
    margin: 0 50px;
    border: 2px white solid;
    border-radius: 50%;
    overflow: hidden;
}

.name_description {
    width: 60%;
    display: flex;
    flex-direction: column;

}

.avatar>img {
    height: 100%;
    width: 100%;
}

.header h1 {
    font-size: 50px;
    font-weight: 400;
    color: rgb(255, 255, 255);
}

.contactInfo>a {
    text-decoration: none;
    color: white;
    font-size: 17px;
}

.description {
    margin: 20px 0;
    width: 80%;
    font-size: 20px;
    color: white;
    border-radius: 10px;
    position: relative;
    opacity: 0;
    animation-name: textingAffect;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    animation-delay: 0.5s;
}

.name_description>button {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    border: none;
    font-size: 15px;
    padding: 10px 0;
    background: none;
    border: 1px solid white;
    cursor: pointer;
    transition: all 0.4s;
    max-width: 100px;

}

.name_description>button>a {
    color: white;
    text-decoration: none;
}

.name_description>button:hover {
    background-color: rgba(255, 255, 255, 0.5);
    color: rgba(39, 39, 39);
}

.scrollDown_btn {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgba(245, 244, 244, 0.8);
    transition: all 0.4s;
    cursor: pointer;
}

.scrollDown_btn:hover {
    color: white;
}

@keyframes textingAffect {
    0% {
        opacity: 0%;
        transform: scaleX(0.8) translateY(20%)
    }

    100% {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}


@media screen and (max-width:1000px) {
    .header {
        flex-direction: column;
        padding: 0;
    }

    .avatar {
        height: 10rem;
        width: 10rem;
    }

    .name_description {
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    .name_description>h1 {
        font-size: 30px;
        text-align: center;
        margin-top: 20px;
    }

    .description {
        width: 90%;
        margin-left: 20px;
    }

    .name_description>button {
        align-self: center;
        width: 200px;
    }
}